:export {
  state: #767676;
  bodyGray: #4F4F4F; }

/* Wrapper */
.note-assistant-wrapper {
  display: flex;
  flex-direction: column;
  width: 175px;
  margin-left: auto;
  min-height: 400px !important;
  /* Notes */
  /* Sort */ }
  .note-assistant-wrapper .note-assistant-content-wrapper {
    margin-top: 5px;
    overflow-y: auto;
    overflow-x: hidden;
    /* 92px is the height of the on large screens Patient Control Panel,
           20px is the height of the top margin of post encounter view content and a little extra for potential scroll bars
           5px is the height of the container's top margin
           42px is the height of the toggle buttons
           60px is the height of the delete button and action buttons
        */
    max-height: calc(100vh - 92px - 20px - 5px - 42px - 60px); }
  .note-assistant-wrapper .toggle-buttons-container {
    display: flex !important;
    margin-right: auto;
    width: inherit;
    max-width: 95%;
    min-height: 41px;
    /* Buttons */ }
    .note-assistant-wrapper .toggle-buttons-container .toggle-button {
      min-width: 50px !important;
      min-height: 20px !important;
      max-height: 41px !important;
      width: 50% !important;
      display: inline !important;
      background-color: #FFFFFF !important;
      box-shadow: none;
      border: 1px solid #DDDDDD; }
      .note-assistant-wrapper .toggle-buttons-container .toggle-button .colored-fill {
        fill: #9A9A9A; }
      .note-assistant-wrapper .toggle-buttons-container .toggle-button g {
        stroke: #9A9A9A; }
      .note-assistant-wrapper .toggle-buttons-container .toggle-button.selected {
        background-color: #028DEA !important; }
        .note-assistant-wrapper .toggle-buttons-container .toggle-button.selected .colored-fill {
          fill: #FFFFFF; }
        .note-assistant-wrapper .toggle-buttons-container .toggle-button.selected g {
          stroke: #FFFFFF; }
      .note-assistant-wrapper .toggle-buttons-container .toggle-button.disabled {
        background-color: #F0F0F0 !important;
        border-color: #F0F0F0; }
        .note-assistant-wrapper .toggle-buttons-container .toggle-button.disabled .colored-fill {
          fill: #9A9A9A; }
        .note-assistant-wrapper .toggle-buttons-container .toggle-button.disabled g {
          stroke: #9A9A9A; }
    .note-assistant-wrapper .toggle-buttons-container .first-button {
      border-radius: 4px 0px 0px 4px; }
    .note-assistant-wrapper .toggle-buttons-container .inside-button {
      border-radius: 0px;
      margin: 0px -1px; }
    .note-assistant-wrapper .toggle-buttons-container .last-button {
      border-radius: 0px 4px 4px 0px; }
  .note-assistant-wrapper .clinical-notes-panel {
    margin-left: auto;
    width: inherit;
    margin-right: 5px; }
    .note-assistant-wrapper .clinical-notes-panel .previous-notes-label {
      color: #767676;
      font-weight: 600;
      font-size: 0.75rem;
      margin: 10px;
      max-width: 184px; }
    .note-assistant-wrapper .clinical-notes-panel .note {
      position: relative;
      width: inherit;
      max-width: 150px;
      padding: 0.5em 1em;
      margin: 1em 0;
      background: #FFFFFF;
      border: 1px solid #DDDDDD;
      border-radius: 5px 0 5px 5px; }
      .note-assistant-wrapper .clinical-notes-panel .note > * {
        font-size: 0.75rem;
        color: #4F4F4F; }
      .note-assistant-wrapper .clinical-notes-panel .note:hover {
        cursor: pointer;
        background-color: #F0F0F0; }
      .note-assistant-wrapper .clinical-notes-panel .note::before {
        content: "";
        position: absolute;
        top: -2px;
        right: -2px;
        border-width: 0 16px 16px 0;
        border-style: solid;
        border-color: #DDDDDD #fff; }
      .note-assistant-wrapper .clinical-notes-panel .note.note-new {
        /*margin: 10px 20px;*/
        width: auto;
        text-align: center;
        display: block; }
        .note-assistant-wrapper .clinical-notes-panel .note.note-new:hover {
          cursor: pointer;
          background: #F0F0F0; }
        .note-assistant-wrapper .clinical-notes-panel .note.note-new .note-new-text {
          font-size: 0.875rem; }
          .note-assistant-wrapper .clinical-notes-panel .note.note-new .note-new-text .fa {
            padding-right: 10px; }
      .note-assistant-wrapper .clinical-notes-panel .note.selected {
        border-width: 2px;
        border-color: #028DEA; }
      .note-assistant-wrapper .clinical-notes-panel .note.search-result {
        background-color: #ffff03; }
      .note-assistant-wrapper .clinical-notes-panel .note.highlighted-result {
        background-color: #ff9632; }
      .note-assistant-wrapper .clinical-notes-panel .note.in-progress-note {
        border-width: 1px;
        border-style: dashed; }
        .note-assistant-wrapper .clinical-notes-panel .note.in-progress-note .in-progress-text {
          font-weight: 600; }
        .note-assistant-wrapper .clinical-notes-panel .note.in-progress-note .in-progress-note-date {
          padding-bottom: 5px;
          font-weight: 600; }
        .note-assistant-wrapper .clinical-notes-panel .note.in-progress-note .in-progress-note-subject {
          padding-bottom: 5px;
          border-bottom: 1px solid #DDDDDD;
          color: #767676; }
        .note-assistant-wrapper .clinical-notes-panel .note.in-progress-note .in-progress-note-author {
          padding-top: 5px;
          padding-bottom: 1px;
          color: #767676; }
        .note-assistant-wrapper .clinical-notes-panel .note.in-progress-note .existing-note-metadata {
          margin-bottom: 5px;
          color: #767676; }
      .note-assistant-wrapper .clinical-notes-panel .note.existing-note .existing-note-date {
        padding-bottom: 5px;
        font-weight: 600; }
      .note-assistant-wrapper .clinical-notes-panel .note.existing-note .existing-note-subject {
        padding-bottom: 10px;
        border-bottom: 1px solid #DDDDDD;
        color: #767676; }
      .note-assistant-wrapper .clinical-notes-panel .note.existing-note .existing-note-author {
        padding-top: 5px;
        padding-bottom: 1px;
        color: #767676; }
      .note-assistant-wrapper .clinical-notes-panel .note.existing-note .existing-note-metadata {
        margin-bottom: 5px;
        color: #767676; }
    .note-assistant-wrapper .clinical-notes-panel .more-notes-btn {
      background-color: #FFFFFF !important;
      padding: 10px !important;
      margin-top: 20px;
      max-width: 184px; }
      .note-assistant-wrapper .clinical-notes-panel .more-notes-btn:hover {
        background: #F0F0F0 !important; }
  .note-assistant-wrapper #delete-note-container {
    background: #FFFFFF;
    display: flex;
    justify-content: left;
    position: absolute;
    bottom: 0;
    width: 175px; }
    .note-assistant-wrapper #delete-note-container #delete-note-button {
      padding: 10px;
      margin-bottom: 10px;
      margin-left: 5px;
      text-transform: none;
      background-color: #FFFFFF;
      border: 1px solid #DDDDDD;
      border-radius: 3px; }
      .note-assistant-wrapper #delete-note-container #delete-note-button:hover {
        background-color: #F0F0F0; }
      .note-assistant-wrapper #delete-note-container #delete-note-button #trash-icon {
        margin-right: 10px;
        color: #C80B0B; }
  .note-assistant-wrapper .sort-label {
    margin-left: 10px;
    font-size: 0.8em; }
  .note-assistant-wrapper .sort-selection {
    margin-bottom: 20px;
    max-width: 184px; }
  .note-assistant-wrapper .sort-select {
    width: 90%;
    max-width: 200px;
    margin-left: 10px; }
  .note-assistant-wrapper .sort-selection .sort-select {
    font-size: 0.8em; }
  .note-assistant-wrapper li.sort-item:hover {
    background-color: #028DEA;
    color: #FFFFFF; }

@media only screen and (min-width: 1200px) and (max-width: 1300px) {
  .note-assistant-wrapper {
    width: 170px; }
    .note-assistant-wrapper #delete-note-container {
      width: 170px; } }

@media only screen and (max-width: 1200px) {
  .note-assistant-wrapper {
    /* 117px is the height of the on large screens Patient Control Panel, 20px is the height of the top margin of post encounter view content and a little extra for potential scroll bars*/
    max-height: calc(100vh - 117px - 20px); } }

@media only screen and (min-width: 577px) and (max-width: 768px) {
  .note-assistant-wrapper {
    width: 160px; }
    .note-assistant-wrapper #delete-note-container {
      width: 160px; } }
