:export {
  state: #767676;
  bodyGray: #4F4F4F; }

.minimap-viewport {
  position: absolute;
  box-sizing: border-box;
  background-color: rgba(0, 0, 0, 0.4);
  z-index: 1;
  cursor: move;
  -webkit-transition: width .5s, height .5s;
  /* Safari */
  transition: width .5s, height .5s; }

.minimap {
  float: right;
  position: fixed;
  right: 0;
  z-index: 1;
  margin: 10px;
  background-color: rgba(0, 0, 0, 0.29);
  border: 1px solid rgba(0, 0, 0, 0.17); }

.minimap-container {
  overflow: scroll;
  width: 100%;
  height: 100%; }

.minimap-children-wrapper {
  position: absolute;
  left: 0; }

.minimap-children {
  background: #CCC;
  border: 1px solid black;
  box-sizing: border-box; }

.edit-button {
  border: 1px solid #767676 !important;
  color: #0079CA;
  margin-left: 4px; }

.edit_visible {
  border: 1px solid rgba(2, 141, 234, 0.4) !important;
  color: #232323 !important; }

.edit_invisible {
  border: none !important;
  background-color: #F9F9F9 !important;
  color: #767676 !important; }
