:export {
  state: #767676;
  bodyGray: #4F4F4F; }

.template-option {
  position: relative;
  width: inherit;
  text-align: left;
  width: 150px;
  height: 90px;
  padding: 0.5em 1em;
  margin: 1em 1em 1em 0;
  background: #FFFFFF;
  border: 1px solid #DDDDDD;
  border-radius: 5px 0 5px 5px; }
  .template-option > * {
    font-size: 0.75rem;
    color: #4F4F4F; }
  .template-option:hover {
    cursor: pointer;
    background-color: #F0F0F0; }
  .template-option::before {
    content: "";
    position: absolute;
    top: -2px;
    right: -2px;
    border-width: 0 16px 16px 0;
    border-style: solid;
    border-color: #DDDDDD #fff; }
  .template-option .template-title {
    padding-bottom: 5px;
    border-bottom: 1px solid #DDDDDD;
    color: #767676;
    text-overflow: ellipsis; }
  .template-option .template-author {
    padding-top: 5px;
    padding-bottom: 1px;
    color: #767676;
    text-overflow: ellipsis; }

.popover-style {
  background-color: #FFFFFF !important;
  border-radius: 5px;
  width: 50% !important;
  height: 35% !important;
  left: 41% !important;
  top: 50% !important;
  padding: 20px 20px 25px 20px;
  margin: 5px;
  overflow-y: auto !important;
  font-size: 0.75rem; }
  .popover-style .popover-container {
    position: relative !important;
    height: 100%;
    overflow-y: auto; }
    .popover-style .popover-container .bolded {
      font-weight: bold; }
    .popover-style .popover-container .close {
      color: #028DEA;
      position: fixed;
      top: 2%;
      right: 2%; }
    .popover-style .popover-container .clickable {
      cursor: pointer; }
    .popover-style .popover-container .choose-template {
      color: #028DEA;
      position: fixed;
      bottom: 2%;
      right: 2%; }
  .popover-style > ::-webkit-scrollbar-track {
    background-color: #FFFFFF; }
