:export {
  state: #767676;
  bodyGray: #4F4F4F; }

#point-of-care-dashboard-content {
  display: flex;
  margin-left: -12px;
  position: relative; }
  #point-of-care-dashboard-content .poc-nav-bar {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    flex-basis: 92px;
    height: calc(100vh - 107px);
    background-color: #32556C;
    position: relative;
    z-index: 3; }
    #point-of-care-dashboard-content .poc-nav-bar button {
      padding: 75px 0px;
      min-width: 79px; }
    #point-of-care-dashboard-content .poc-nav-bar .content {
      margin: auto;
      overflow-y: hidden; }
    #point-of-care-dashboard-content .poc-nav-bar .next-patient {
      margin: 0; }
  #point-of-care-dashboard-content .content {
    width: 92px; }
  #point-of-care-dashboard-content .right-border-box {
    margin: 15px 10px 5px;
    overflow: hidden; }
    #point-of-care-dashboard-content .right-border-box.point-of-care-container {
      overflow-y: auto;
      overflow-x: hidden;
      text-align: left;
      /* 106px is the height of the on large screens Patient Control Panel, 20px is the height of the top margin of post encounter view content and a little extra for potential scroll bars*/
      max-height: calc(100vh - 92px - 15px);
      min-height: 400px !important;
      margin-right: -2px; }
    #point-of-care-dashboard-content .right-border-box .fitted-panel {
      overflow-y: auto;
      overflow-x: hidden;
      text-align: left;
      /* 106px is the height of the on large screens Patient Control Panel, 20px is the height of the top margin of post encounter view content and a little extra for potential scroll bars*/
      max-height: calc(100vh - 117px - 20px + 10px);
      min-height: 400px !important;
      /* have to manually calculate the width due to the sidebar: 92 for the sidebar, 80 for the minimap and 42 for additional padding */
      width: calc(100vw - 92px - 80px - 42px); }
  #point-of-care-dashboard-content .full-panel {
    height: auto; }

@media only screen and (max-width: 1200px) {
  #point-of-care-dashboard-content .point-of-care-container {
    max-height: calc(100vh - 117px - 20px + 10px) !important; } }
