:export {
  state: #767676;
  bodyGray: #4F4F4F; }

.Pilot2MvpApp-content {
  min-height: 100vh;
  background-color: #FFFFFF;
  overflow: hidden;
  /* Reduce padding that is set using fluid-container from react flex box */
  padding-right: 0rem !important;
  padding-left: 0rem !important; }
  .Pilot2MvpApp-content.loading-background {
    background: grey; }
  .Pilot2MvpApp-content .container-fluid {
    padding-left: 12px;
    padding-right: 12px; }
  .Pilot2MvpApp-content .panel-content {
    padding: 5px;
    text-align: left; }
  .Pilot2MvpApp-content .dashboard-panel {
    text-align: left; }
  .Pilot2MvpApp-content .divider {
    margin: 30px 0px 24px 0px !important;
    clear: both; }
  .Pilot2MvpApp-content h1 {
    margin: 0px;
    font-size: 1.0rem;
    font-weight: 400; }
  .Pilot2MvpApp-content h2 {
    margin: 0px;
    font-size: 1rem;
    font-weight: 600; }
  .Pilot2MvpApp-content h3 {
    margin: 2px 0;
    font-size: 0.8125rem;
    font-weight: 400; }
  .Pilot2MvpApp-content ::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 1px rgba(0, 0, 0, 0);
    border-radius: 10px;
    background-color: #FFFFFF; }

#modal-title {
  margin-bottom: 10px;
  font-size: 1.1rem;
  font-weight: 400; }

#simple-modal-description {
  margin: 0px; }

.content-disabled {
  pointer-events: none;
  opacity: 0.4; }
