:export {
  state: #767676;
  bodyGray: #4F4F4F; }

.line-chart-subsection .subsection-heading h2 {
  margin: 10px 0; }
  .line-chart-subsection .subsection-heading h2 .subsection-name {
    font-weight: 400;
    font-size: 0.8125rem; }
  .line-chart-subsection .subsection-heading h2 .subsection-icons {
    margin-left: 5px; }
    .line-chart-subsection .subsection-heading h2 .subsection-icons .small-btn {
      min-width: 0 !important;
      min-height: 0 !important; }

.line-chart-subsection .no-entries {
  padding: 20px 0px 10px 0px;
  font-size: 1rem;
  font-weight: 400;
  color: #767676; }

.line-chart-subsection .recharts-responsive-container .recharts-tooltip-wrapper {
  font-size: 0.8125rem; }

.line-chart-subsection .recharts-responsive-container .recharts-text {
  font-size: 0.75rem; }

.line-chart-subsection .recharts-responsive-container .hide-line .recharts-curve {
  stroke: none; }
  .line-chart-subsection .recharts-responsive-container .hide-line .recharts-curve.recharts-tooltip-cursor {
    stroke: #CCCCCC; }
