.tooltip-progression-form {
    position: relative;
    display: inline-block;
}

.tooltip-progression-form .tooltiptext {
    visibility: hidden;
    width: 150px;
    background-color: #555;
    color: #fff;
    text-align: center;
    border-radius: 5px;
    padding: 10px 10px;
    position: absolute;
    z-index: 1;
    bottom: 125%;
    margin-left: 18px !important;
    opacity: 0;
    transition: opacity 1s;
    font-size: .8rem;
}

.tooltip-progression-form .large {
    width: 220px;
    margin-left: -95px;
}

.tooltip-progression-form .tooltiptext::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: #555 transparent transparent transparent;
}

.tooltip-progression-form:hover .tooltiptext {
    visibility: visible;
    opacity: 1;
    transition-delay: 0.5s;
}

.helper-text {
    color: #b1b1b1;
}

.MuiInput-inkbar-113:after{
    background-color: #297DA2 !important;
}

.btn-group-reason-progression { 
    margin: 0 10px;
}

h4 span.helper-text { 
    font-size: .9em;
    font-style: italic;
}