:export {
  state: #767676;
  bodyGray: #4F4F4F; }

#template-option-container {
  text-align: left;
  /* 92x is the height of the on large screens Patient Control Panel, 15px is the height of the top margin of post encounter view content and a little extra for potential scroll bars*/
  max-height: calc(100vh - 92px - 15px - 65px);
  overflow-y: auto; }
  #template-option-container #new-note-container {
    margin: 1em; }
  #template-option-container #template-search-container {
    margin: 1em;
    width: 60%; }
  #template-option-container #available-templates-container {
    margin: 1em 1em 0 1em;
    display: flex;
    flex-wrap: wrap; }
  #template-option-container #cancel-btn-container {
    width: 100%;
    background: #FFFFFF;
    position: fixed;
    bottom: 0;
    height: 65px; }
    #template-option-container #cancel-btn-container #cancel-btn {
      margin: 1em;
      padding: 10px;
      border: 1px solid #DDDDDD;
      background-color: #FFFFFF;
      min-width: 160px; }
      #template-option-container #cancel-btn-container #cancel-btn:hover {
        background-color: #F0F0F0; }

@media only screen and (max-width: 1200px) {
  #template-option-container {
    /* 117px is the height of the on large screens Patient Control Panel, 20px is the height of the top margin of post encounter view content and a little extra for potential scroll bars*/
    max-height: calc(100vh - 117px - 15px - 65px); } }
