:export {
  state: #767676;
  bodyGray: #4F4F4F; }

#clinical-notes {
  padding-left: 25px;
  align-self: flex-start; }
  #clinical-notes #note-description .note-description-detail {
    margin: 0; }
  #clinical-notes #note-description .note-description-detail-name {
    text-align: left;
    padding-bottom: 2px;
    color: #4F4F4F;
    font-size: 0.75rem;
    margin: 0; }
  #clinical-notes #note-description .note-description-detail-value {
    text-align: left;
    padding-top: 0;
    color: #232323;
    font-size: 0.75rem;
    margin: 0; }
  #clinical-notes #note-description p.note-name {
    text-align: left;
    padding-top: 0;
    font-size: 0.75rem;
    margin: 0px;
    padding-left: 0.5rem;
    color: #232323;
    font-weight: 600; }
    #clinical-notes #note-description p.note-name:hover {
      cursor: pointer; }
    #clinical-notes #note-description p.note-name #edit-note-name-btn {
      margin-left: -20px;
      margin-right: 5px;
      margin-bottom: -1px; }
  #clinical-notes #note-description #text-field-container {
    margin: 0px 0px 0px 10px;
    width: 100%; }
  #clinical-notes #note-description #note-title-input {
    font-family: "Open Sans", Arial, sans-serif;
    font-size: .8rem; }
  #clinical-notes #note-description .close-note-btn {
    background-color: white !important;
    padding: 10px !important;
    margin-top: 0px !important;
    text-transform: none !important;
    color: #232323;
    font-weight: 400;
    border: 1px solid #DDDDDD;
    border-radius: 3px; }
    #clinical-notes #note-description .close-note-btn .close-x {
      color: #028DEA;
      margin-right: 5px; }
  #clinical-notes #note-description .close-note-btn:hover {
    background: #E6E6E6 !important; }
  #clinical-notes #note-description .note-description-divider {
    margin: 20px 0px 10px 0px !important;
    clear: both; }
  #clinical-notes .MyEditor-root {
    font: 400 0.875rem "Open Sans", Arial, sans-serif; }
    #clinical-notes .MyEditor-root .toolbar-menu > * {
      display: inline-block; }
    #clinical-notes .MyEditor-root .toolbar-menu {
      padding: 1px 0 10px 5px;
      margin-bottom: 20px;
      border-bottom: 1px solid #ddd; }
      #clinical-notes .MyEditor-root .toolbar-menu .button {
        color: #ccc;
        cursor: pointer; }
      #clinical-notes .MyEditor-root .toolbar-menu .button[data-active="true"] {
        color: #232323; }
    #clinical-notes .MyEditor-root p {
      display: inline;
      margin: 0; }
    #clinical-notes .MyEditor-root span {
      display: inline; }
    #clinical-notes .MyEditor-root .editor-content {
      /* 96px is the height of the Patient Control Panel, 20px is the height of the top margin of post encounter view
            content and a little extra for potential scroll bars, 130px accounts for the editor header and editor toolbar
            plus margins, 76px is the height of the sign button, 20px is the margin on the sign button */
      height: calc(100vh - 96px - 20px - 130px - 76px - 20px) !important;
      overflow-y: auto;
      overflow-x: hidden;
      text-align: left; }
      #clinical-notes .MyEditor-root .editor-content .placeholder {
        background-color: #dddddd; }
      #clinical-notes .MyEditor-root .editor-content .structured-field-bolded {
        font-weight: 600; }
      #clinical-notes .MyEditor-root .editor-content .structured-field-inserter {
        color: #767676;
        display: inline; }
        #clinical-notes .MyEditor-root .editor-content .structured-field-inserter-incomplete {
          color: #767676;
          display: inline;
          border-bottom: 1.5px dashed #EC9696; }
      #clinical-notes .MyEditor-root .editor-content .structured-field-creator {
        border-bottom: 1px solid #55C9CE; }
        #clinical-notes .MyEditor-root .editor-content .structured-field-creator-incomplete {
          border-bottom: 1.5px dashed #EC9696; }
      #clinical-notes .MyEditor-root .editor-content .structured-field-search-result {
        background-color: #ffff03; }
      #clinical-notes .MyEditor-root .editor-content .structured-field-selected-search-result {
        background-color: #ff9632; }
      #clinical-notes .MyEditor-root .editor-content .search-result-regular-highlight {
        background-color: #ffff03; }
      #clinical-notes .MyEditor-root .editor-content .search-result-selected-highlight {
        background-color: #ff9632; }
      #clinical-notes .MyEditor-root .editor-content .in-progress-note div:last-child {
        padding-bottom: 2px; }
      #clinical-notes .MyEditor-root .editor-content .in-progress-note .structured-field-creator {
        border-bottom-style: dashed;
        border-bottom-width: 1.5px; }
  @media only screen and (max-width: 1024px) {
    #clinical-notes .editor-content {
      /* 117px is the height of the Patient Control Panel, 20px is the height of the top margin of post encounter view
            content and a little extra for potential scroll bars, 130px accounts for the editor header and editor toolbar
            plus margins, 76px is the height of the sign button, 20px is the margin on the sign button */
      height: calc(100vh - 117px - 20px - 130px - 76px - 20px) !important; } }

.placeholder-data {
  border-bottom: 1.5px dashed #55C9CE;
  line-height: 140%; }

.placeholder-data-signed {
  border-bottom: 1px solid #55C9CE;
  line-height: 140%; }

.placeholder-data-missing {
  border-bottom: 1.5px dashed #EC9696;
  font-style: italic;
  line-height: 140%; }

.placeholder-data-missing-signed {
  border-bottom: 1px solid #EC9696;
  font-style: italic;
  line-height: 140%; }

.suggestion-portal {
  position: absolute;
  box-sizing: border-box;
  border-radius: 2px;
  margin-top: 30px;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 6px, rgba(0, 0, 0, 0.12) 0px 1px 4px;
  transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
  background-color: white;
  z-index: 1111;
  max-height: 212px;
  /* 6 list elements at 32px each, plus 10px of margin on top and bottom */
  overflow-y: auto; }
  .suggestion-portal ul {
    list-style-type: none;
    padding: 0;
    margin: 10px 0;
    background-color: #fff; }
  .suggestion-portal > * {
    display: block;
    padding: 7px 10px; }
  .suggestion-portal li {
    padding: 5px 10px;
    min-width: 75px;
    max-width: 300px; }
  .suggestion-portal li.selected {
    background-color: #028DEA;
    color: #fff; }
