:export {
  state: #767676;
  bodyGray: #4F4F4F; }

.similar-patients-selector__select-buttons button {
  background-color: #FFFFFF;
  border: 0;
  cursor: pointer;
  color: #028DEA;
  font-size: 0.9em;
  font-weight: 600; }
  .similar-patients-selector__select-buttons button:hover {
    color: #026fb7; }
  .similar-patients-selector__select-buttons button:focus {
    outline: none;
    background-color: #028DEA;
    color: #fff;
    border-radius: 5px; }

.similar-patients-selector .selection-option {
  display: flex;
  flex-direction: column;
  margin: 20px 0; }

.similar-patients-selector__options {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap; }
  .similar-patients-selector__options .options-checkbox-list {
    flex: 0 1 calc(33% - 1em); }
