:export {
  state: #767676;
  bodyGray: #4F4F4F; }

.icons-chart {
  display: flex;
  justify-content: center;
  margin-top: 40px; }
  .icons-chart .icons-survival-text .selected-treatment {
    font-weight: 400; }
  .icons-chart .icons-survival-text .num-more-survive,
  .icons-chart .icons-survival-text .num-less-survive,
  .icons-chart .icons-survival-text .num-zero-more-survive {
    font-size: 1.3em;
    font-weight: 500; }
  .icons-chart .icons-survival-text .num-more-survive {
    color: #427E84; }
  .icons-chart .icons-survival-text .num-less-survive {
    color: #EA7E73; }
  .icons-chart__display {
    display: flex;
    flex-wrap: wrap;
    width: 400px;
    height: 300px; }
  .icons-chart__circle {
    border-radius: 50%;
    height: 25px;
    width: 25px;
    margin: 6px; }
    .icons-chart__circle.survive {
      border: 1px solid #4F4F4F; }
    .icons-chart__circle.additional {
      border: 3px solid #427E84;
      margin: 4px; }
    .icons-chart__circle.die, .icons-chart__circle.less {
      border: 1px solid #9A9A9A;
      background-color: #9A9A9A; }
    .icons-chart__circle.less {
      border: 3px solid #EA7E73;
      margin: 4px; }
  .icons-chart.helper-text {
    font-style: italic; }
