:export {
  state: #767676;
  bodyGray: #4F4F4F; }

#targeted-data-section h2.section-header {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: space-between;
  font-weight: 600;
  color: #232323;
  margin-bottom: 14px; }

.section-header__highlighted {
  background-color: #ffff03; }

.section-header__selected {
  background-color: #ff9632; }

.section-header__condition {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  color: #767676;
  font-weight: normal;
  flex: 1;
  padding-right: 10px;
  margin-left: 10px; }
  .section-header__condition.encounter {
    text-overflow: inherit;
    overflow: inherit;
    white-space: normal; }

.small-btn {
  min-width: 0 !important;
  min-height: 0 !important; }

.expansion-instructions {
  margin-left: 5px;
  font-style: italic;
  color: #333;
  font-size: 13px;
  vertical-align: middle;
  display: inline-flex;
  align-items: center; }

.right-icons {
  margin-right: 10px;
  display: flex; }
  .right-icons .small-btn {
    margin-left: 10px !important; }

.criterion-summary {
  margin-left: 8px; }

.menu-list ul {
  padding: 0px; }
  .menu-list ul li {
    padding: 5px 10px; }
    .menu-list ul li h3 {
      font-size: 13px; }
    .menu-list ul li .checkbox {
      font-size: 1rem !important; }
