:export {
  state: #767676;
  bodyGray: #4F4F4F; }

#summary-header {
  display: flex;
  align-items: center;
  min-height: 80px; }
  #summary-header #summary-header .FullApp-content {
    width: 100%; }
  #summary-header .patient-item {
    margin-left: 1px !important;
    font-size: .7em;
    color: #999; }
  #summary-header .patient-name {
    font-size: 15px !important;
    margin-bottom: 15px; }
  #summary-header .tablet-patient-name {
    font-weight: 600;
    font-size: 20px !important; }
  #summary-header .tablet-patient-info {
    color: #4F4F4F !important;
    font-size: 0.8125rem; }
  #summary-header .patient-mrn {
    color: #999; }
  #summary-header .clickable {
    cursor: pointer; }
  #summary-header .fa-angle-double-down {
    margin-left: 10px;
    color: #378EE3; }
  #summary-header .avatar {
    display: inline-block;
    padding-right: 15px; }
  #summary-header .patient-info {
    display: inline-block; }
  #summary-header .item {
    text-align: left; }
  #summary-header .item span {
    font-size: 0.8rem; }
  #summary-header .no-wrap {
    white-space: nowrap; }
  #summary-header .small-btn {
    min-width: 0 !important;
    min-height: 0 !important;
    margin-left: 1px; }
