:export {
  state: #767676;
  bodyGray: #4F4F4F; }

.expanded-tables-visualizer {
  margin-bottom: 30px;
  max-width: 840px; }
  .expanded-tables-visualizer .expanded-table {
    margin-right: 20px;
    font-size: 0.8125rem; }
    .expanded-tables-visualizer .expanded-table:first-child {
      margin-top: 10px; }
    .expanded-tables-visualizer .expanded-table:nth-child(odd) .expanded-table-header {
      background-color: #F9F9F9; }
    .expanded-tables-visualizer .expanded-table:nth-child(even) .expanded-table-header {
      background-color: #FFFFFF; }
    .expanded-tables-visualizer .expanded-table-header {
      font-weight: 600;
      height: inherit;
      padding: 8px 10px;
      margin-bottom: 2px; }
      .expanded-tables-visualizer .expanded-table-header .row div:first-child span:first-child {
        color: #55C9CE;
        width: 25px;
        cursor: pointer; }
      .expanded-tables-visualizer .expanded-table-header .row div:last-child {
        text-align: right; }
    .expanded-tables-visualizer .expanded-table-body {
      margin-left: 25px;
      background-color: #FFFFFF; }
      .expanded-tables-visualizer .expanded-table-body table {
        border-collapse: collapse; }
      .expanded-tables-visualizer .expanded-table-body tr {
        height: inherit; }
      .expanded-tables-visualizer .expanded-table-body td {
        padding: 8px 10px;
        border: 0;
        color: #232323;
        font-family: "Open Sans", Arial, sans-serif; }
      .expanded-tables-visualizer .expanded-table-body tbody tr:nth-child(odd) {
        background-color: #F9F9F9; }
      .expanded-tables-visualizer .expanded-table-body tbody tr:nth-child(even) {
        background-color: #FFFFFF; }
      .expanded-tables-visualizer .expanded-table-body .positive-questions-table {
        margin-bottom: 5px; }
        .expanded-tables-visualizer .expanded-table-body .positive-questions-table td {
          width: 25%; }
          .expanded-tables-visualizer .expanded-table-body .positive-questions-table td:last-child {
            width: 75%; }
      .expanded-tables-visualizer .expanded-table-body .negative-questions-table td {
        width: 50%; }
        .expanded-tables-visualizer .expanded-table-body .negative-questions-table td:last-child {
          text-align: right; }
    .expanded-tables-visualizer .expanded-table span.highlighted {
      background-color: #ffff03 !important; }
    .expanded-tables-visualizer .expanded-table span.selected {
      background-color: #ff9632 !important; }
    .expanded-tables-visualizer .expanded-table span.list-missing {
      border-bottom: 1px solid #EC9696;
      color: #767676; }
    .expanded-tables-visualizer .expanded-table span.list-unsigned {
      border-bottom: 1.5px dashed #55C9CE;
      text-decoration: none; }
    .expanded-tables-visualizer .expanded-table span.list-captured {
      border-bottom: 1px solid #55C9CE; }
    .expanded-tables-visualizer .expanded-table span.has-action-menu {
      cursor: pointer; }
