:export {
  state: #767676;
  bodyGray: #4F4F4F; }

.PointOfCareApp-content {
  min-height: 100vh;
  background-color: #FFFFFF;
  overflow: hidden;
  /* Reduce padding that is set using fluid-container from react flex box */
  padding-right: 0rem !important;
  padding-left: 0rem !important; }
  .PointOfCareApp-content.loading-background {
    background: grey; }
  .PointOfCareApp-content .container-fluid {
    padding-left: 0px !important;
    padding-right: 0px !important; }
  .PointOfCareApp-content .panel-content {
    padding: 5px 17px 0px;
    text-align: left; }
  .PointOfCareApp-content .dashboard-panel {
    text-align: left; }
  .PointOfCareApp-content .divider {
    margin: 30px 0px 24px 0px !important;
    clear: both; }
  .PointOfCareApp-content h1 {
    margin: 0px;
    font-size: 1rem;
    font-weight: 400; }
  .PointOfCareApp-content h2 {
    margin: 0px;
    font-size: 1rem;
    font-weight: 600; }
  .PointOfCareApp-content h3 {
    margin: 2px 0;
    font-size: 0.8125rem;
    font-weight: 400; }
  .PointOfCareApp-content ::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 1px rgba(0, 0, 0, 0);
    border-radius: 10px;
    background-color: #FFFFFF; }

#modal-title {
  margin-bottom: 10px;
  font-size: 1rem;
  font-weight: 600; }

#simple-modal-description {
  margin: 0px; }

.content-disabled {
  pointer-events: none;
  opacity: 0.4; }

.header .close-div {
  background-color: white !important;
  font-weight: 400;
  border: 1px solid #DDDDDD;
  border-radius: 3px;
  color: #232323;
  border-width: 1px;
  float: right;
  padding: 10px;
  margin-top: 0px !important;
  text-transform: none !important; }
  .header .close-div:hover {
    background: #e6e6e6 !important;
    cursor: pointer; }
  .header .close-div .close-button {
    color: #028DEA;
    margin-right: 5px; }
  .header .close-div .close-text {
    float: right;
    margin-left: 5px; }
