:export {
  state: #767676;
  bodyGray: #4F4F4F; }

.options-checkbox-list {
  margin: 10px 0; }
  .options-checkbox-list__header {
    white-space: nowrap; }
    .options-checkbox-list__header .expand-icon {
      margin-right: 10px;
      cursor: pointer;
      font-size: 1.2em;
      color: #767676; }
    .options-checkbox-list__header .header-title {
      font-weight: 600;
      color: #4F4F4F; }
    .options-checkbox-list__header .selected-count {
      margin-left: 10px;
      font-size: 0.9em;
      color: #767676; }
  .options-checkbox-list__options .selection-options {
    display: flex;
    flex-direction: column;
    margin-left: 60px; }
    .options-checkbox-list__options .selection-options__title {
      color: #4F4F4F; }
    .options-checkbox-list__options .selection-options__selection {
      flex-direction: row;
      align-items: center; }
    .options-checkbox-list__options .selection-options .range {
      margin-left: -10px;
      color: #0079CA;
      font-size: 0.875rem;
      font-weight: 400;
      cursor: pointer; }
  .options-checkbox-list .checkbox {
    height: 22px;
    width: 22px;
    margin-right: 5px;
    color: #298085; }
