:export {
  state: #767676;
  bodyGray: #4F4F4F; }

.narrative-subsections {
  margin-bottom: 20px;
  font-size: 0.8rem; }

.narrative-inserter-tooltip ul {
  padding: 0; }

li.narrative-inserter-box {
  padding: 5px 10px; }
  li.narrative-inserter-box .fa {
    height: 10px;
    font-size: 10px;
    margin-right: 0px; }
  li.narrative-inserter-box h3 {
    font-size: 13px; }
  li.narrative-inserter-box:hover {
    background-color: #1384b5;
    color: #FFFFFF; }
    li.narrative-inserter-box:hover h3 {
      color: #FFFFFF; }
    li.narrative-inserter-box:hover .fa {
      color: #FFFFFF; }

div.narrative-inserter-menu-item {
  padding: 0; }

span.narrative-missing-data {
  border-bottom: 1px solid #EC9696;
  text-decoration: none; }

span.narrative-structured-data {
  border-bottom: 1px solid #55C9CE;
  text-decoration: none; }

span.narrative-unsigned-data {
  border-bottom: 1.5px dashed #55C9CE;
  text-decoration: none; }

span.narrative.highlighted {
  background-color: #ffff03 !important; }

span.narrative.selected {
  background-color: #ff9632 !important; }

span.has-action-menu {
  cursor: pointer; }

.menu-item {
  font-size: 0.2em; }
