:export {
  state: #767676;
  bodyGray: #4F4F4F; }

.expanded-style {
  padding-bottom: 24px;
  /* Shows calendar picker modal over every other component */ }
  .expanded-style .checkbox-container {
    display: flex;
    align-items: center;
    justify-content: left; }
  .expanded-style .done-checkbox {
    margin-inline-end: 5px;
    vertical-align: top;
    color: #767676;
    font-weight: 400;
    font-size: 13px; }
  .expanded-style .expand-icon {
    right: unset !important;
    transform: rotate(-90deg) !important; }
  .expanded-style .close-expand-icon {
    transform: rotate(180deg) !important; }
  .expanded-style .summary-panel div[role='button'] {
    left: -15px !important;
    top: 24px; }
  .expanded-style .shortcut-name {
    margin-inline-end: 10px;
    vertical-align: top;
    font-weight: 600;
    font-size: 13px;
    color: #232323; }
  .expanded-style .error-message {
    color: #ff0000; }
  .expanded-style .shortcut-field-title {
    margin-inline-end: 5px;
    font-size: 0.9rem; }
  .expanded-style .fill-missing-data {
    margin-inline-end: 20px;
    border-bottom: 1px solid #EC9696;
    text-decoration: none;
    font-size: 0.9rem; }
  .expanded-style .fill-structured-data {
    margin-inline-end: 20px;
    border-bottom: 1.5px dashed #55C9CE;
    text-decoration: none;
    font-size: 0.9rem; }
  .expanded-style .date-picker-button {
    border: 1px solid #555555;
    width: 175px;
    display: inline-block;
    color: #555555;
    background-color: white;
    padding: 5px;
    border-radius: 3px;
    cursor: pointer;
    text-align: left; }
  .expanded-style .date-picker-container {
    position: relative; }
  .expanded-style .date-picker-container > * {
    z-index: 999; }
  .expanded-style .arrow-container {
    float: right;
    margin-right: 5px; }
  .expanded-style .date-picker-button i {
    border: solid 1px black;
    border-width: 0 1px 1px 0;
    padding: 3px;
    float: right; }
  .expanded-style .arrow-down {
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg); }
  .expanded-style #poc-divider {
    width: 83.3%;
    margin-left: 130px !important; }
  .expanded-style .attribute-title {
    font-size: 13px;
    font-weight: 400;
    color: #232323; }
  .expanded-style .centered-field {
    align-items: center; }
  .expanded-style .not-centered-field {
    align-items: baseline !important;
    padding-top: 23.5px !important; }
  .expanded-style .field-row {
    margin-top: 5px !important; }
  .expanded-style .multi-select {
    font-size: 11px;
    font-weight: 400;
    color: #232323; }
  @media only screen and (max-width: 1024px) {
    .expanded-style .checkbox-container {
      flex-wrap: wrap-reverse; } }
  .expanded-style .link {
    /* $shr-context-dark */
    color: #0084b8;
    cursor: pointer; }

/* Allows calendar picker modal to overflow over ExpansionPanel */
.expanded-style > * {
  overflow: visible !important; }
