:export {
  state: #767676;
  bodyGray: #4F4F4F; }

.medication-visualizer-wrapper {
  padding-top: 10px; }

.medication-item {
  padding: 0px 0; }
  .medication-item-heading, .medication-item-content {
    padding: 3px 10px; }
    .medication-item-heading-title, .medication-item-content-title {
      font-weight: 550; }
    .medication-item-heading .highlighted, .medication-item-content .highlighted {
      background-color: #ffff03 !important; }
    .medication-item-heading .selected, .medication-item-content .selected {
      background-color: #ff9632 !important; }
    .medication-item-heading .normal, .medication-item-content .normal {
      color: #4F4F4F; }
    .medication-item-heading .out-of-range, .medication-item-content .out-of-range {
      color: #C80B0B; }

.medication-range-chart-container {
  width: 100%;
  height: 100%;
  display: flex; }

.medication-info {
  font-size: 12px;
  padding-bottom: 7px; }
  .medication-info-heading {
    color: #999; }

.medication-change {
  font-size: 12px; }
  .medication-change-container {
    display: flex;
    padding-left: 0rem; }
  .medication-change-padding {
    padding-left: 0rem; }
  .medication-change-type {
    font-weight: 700; }
    .medication-change-type-unsigned {
      cursor: pointer;
      border-bottom: 1.5px dashed rgba(1, 130, 215, 0.8);
      text-decoration: none; }
  .medication-change-wide {
    font-size: 12px;
    align-self: center;
    margin: auto; }

.medication-dosage-info {
  font-size: 12px; }
