:export {
  state: #767676;
  bodyGray: #4F4F4F; }

.table-legend {
  text-align: center; }
  .table-legend .wrapper {
    display: inline-block; }
  .table-legend .prog-fill {
    background-size: 6px !important;
    display: inline-block;
    padding: 0;
    height: 10px;
    width: 40px;
    background-color: #9A9A9A; }
  .table-legend .legend-entry {
    display: block;
    text-align: left; }
  .table-legend .prog-fill-container {
    display: inline-block;
    width: 50px; }
    .table-legend .prog-fill-container .main {
      width: 45px; }
    .table-legend .prog-fill-container .treatment-increase {
      width: 10px;
      background-color: #91bd7b; }
    .table-legend .prog-fill-container .treatment-decrease {
      width: 14px;
      background: repeating-linear-gradient(45deg, transparent, transparent 2px, #EC9696 2px, #EC9696 4px), linear-gradient(to bottom, #FFFFFF, #FFFFFF); }
    .table-legend .prog-fill-container .cancer {
      width: 20px; }
  .table-legend .legend-text {
    font-size: 10px;
    padding-left: 10px;
    line-height: 10px; }
  .table-legend .progress-bar {
    display: flex;
    height: 8px;
    border: 1px solid #DDDDDD;
    background-color: #FFFFFF; }
