:export {
  state: #767676;
  bodyGray: #4F4F4F; }

.treatment-options-outcomes-table {
  width: 100%;
  border-collapse: collapse;
  border: 0; }
  .treatment-options-outcomes-table__header td {
    padding: 0 10px;
    font-size: 1.1em; }
  .treatment-options-outcomes-table__header .compare-header {
    font-size: 0.9em;
    padding-right: 40px;
    margin-bottom: -10px;
    min-width: 120px; }
    @media only screen and (max-width: 1024px) {
      .treatment-options-outcomes-table__header .compare-header {
        padding-right: 0; } }
  .treatment-options-outcomes-table__header .user-icon {
    text-align: center; }
  .treatment-options-outcomes-table__header .header-title {
    font-weight: 600;
    font-size: 1.1em;
    margin-bottom: 5px; }
    .treatment-options-outcomes-table__header .header-title #ccp-table-select .custom-select:after {
      background-color: #298085; }
  .treatment-options-outcomes-table__header .sort-arrows {
    visibility: hidden;
    color: #028DEA;
    font-size: 12px;
    padding-left: 5px; }
  .treatment-options-outcomes-table__header .sort-selected {
    visibility: visible;
    color: #48BDBD; }
  .treatment-options-outcomes-table__header .header-space:hover .sort-arrows {
    visibility: visible; }
  .treatment-options-outcomes-table__header .header-space {
    cursor: pointer;
    margin-right: -20px; }
  .treatment-options-outcomes-table__table td {
    padding: 0 10px;
    font-size: 1.1em;
    vertical-align: middle; }
  .treatment-options-outcomes-table__table .select-icon {
    padding-right: 10px;
    cursor: pointer; }
  .treatment-options-outcomes-table__table .treatments-title {
    font-weight: bold; }
  .treatment-options-outcomes-table__table .select-treatments {
    display: inline-block;
    position: relative;
    cursor: pointer;
    margin-left: 20px; }
    .treatment-options-outcomes-table__table .select-treatments .popover-text {
      color: #298085;
      font-weight: bold;
      font-size: 0.9em; }
  .treatment-options-outcomes-table__table .top-side-effects {
    flex-direction: column;
    min-width: 100px; }
  .treatment-options-outcomes-table__table .table-row {
    height: 60px;
    align-items: center; }
    .treatment-options-outcomes-table__table .table-row:nth-child(odd) {
      background-color: #F9F9F9; }
    .treatment-options-outcomes-table__table .table-row.helper-text {
      font-style: italic; }
    .treatment-options-outcomes-table__table .table-row .total-patients {
      text-align: center; }
    .treatment-options-outcomes-table__table .table-row.selected-treatment {
      background-color: #ececec;
      border-top: 1px solid #e0e0e0;
      box-shadow: 0px 10px 8px -5px #999999; }
  .treatment-options-outcomes-table tfoot td {
    padding: 10px 0; }
  .treatment-options-outcomes-table .flex {
    display: flex; }
  .treatment-options-outcomes-table .flex-1 {
    flex: 1; }
  .treatment-options-outcomes-table .flex-2 {
    flex: 2; }
  .treatment-options-outcomes-table .flex-3 {
    flex: 3; }
  .treatment-options-outcomes-table .flex-4 {
    flex: 4; }
  .treatment-options-outcomes-table .flex-6 {
    flex: 6; }
  .treatment-options-outcomes-table .flex-padding {
    padding: 0 20px; }
  .treatment-options-outcomes-table .flex-center {
    align-items: center; }
  @media (max-width: 1024px) {
    .treatment-options-outcomes-table .flex-padding {
      padding: 0 5px; } }

li.side-effect-option:hover {
  color: #FFFFFF; }
