:export {
  state: #767676;
  bodyGray: #4F4F4F; }

.bar-chart {
  min-width: 70px;
  padding: 0;
  font-size: 0.9em; }
  .bar-chart .bar-chart-text {
    display: inline-block;
    margin-bottom: 0;
    margin-top: 0; }
  .bar-chart .right-text {
    float: right;
    padding-right: 5px; }
  .bar-chart .progress-bar {
    display: flex;
    height: 100%;
    position: relative;
    background-color: #FFFFFF;
    width: 100%; }
  .bar-chart .prog-fill {
    background-size: 6px !important;
    display: inline-block;
    padding: 0;
    height: 12px;
    width: 0;
    background-color: #9A9A9A; }
  .bar-chart .prog-fill-empty {
    border-style: solid;
    border-color: #DDDDDD;
    border-width: 1px 1px 1px 0;
    flex-grow: 100; }
    .bar-chart .prog-fill-empty.zero {
      border-width: 1px; }
  .bar-chart .tiny-arrow {
    opacity: 0.75;
    padding-right: 2px; }
  .bar-chart .prog-fill.increase {
    background-color: #91bd7b; }
  .bar-chart .prog-fill.decrease {
    background: repeating-linear-gradient(45deg, transparent, transparent 2px, #EC9696 2px, #EC9696 4px), linear-gradient(to bottom, #767676, #767676); }
  .bar-chart .bar-chart-text.increase {
    color: #5cb85c; }
  .bar-chart .bar-chart-text.decrease {
    color: #d9534f; }
