:export {
  state: #767676;
  bodyGray: #4F4F4F; }

.patient-control-panel {
  width: 100vw;
  position: relative;
  text-align: left;
  left: 50%;
  right: 50%;
  margin-left: -50vw;
  margin-right: -50vw; }
  .patient-control-panel, .patient-control-panel .FullApp-content {
    background-color: #FFFFFF; }
  .patient-control-panel .clickable {
    cursor: pointer; }
  .patient-control-panel .logo-title-column {
    display: flex;
    align-items: center; }
    .patient-control-panel .logo-title-column .logo-accompaniment {
      display: inline-block; }
      .patient-control-panel .logo-title-column .logo-accompaniment .title {
        display: block;
        color: #298085;
        font-size: 1.1rem;
        font-weight: 600;
        padding-left: 5px; }
      .patient-control-panel .logo-title-column .logo-accompaniment .login {
        display: block;
        font-size: 0.8rem;
        margin-left: 0.4rem; }
  .patient-control-panel .summary-header-column {
    padding-left: 0; }
  .patient-control-panel #condition-selection-container {
    padding: 5px; }

.vertical-divider {
  border-left: 1px solid #DDDDDD;
  padding: 0 .5rem; }

.search-icon {
  padding-right: 5px; }
