:export {
  state: #767676;
  bodyGray: #4F4F4F; }

.tabular-list {
  margin-bottom: 20px; }
  .tabular-list table {
    border-collapse: collapse;
    width: auto;
    margin-top: 10px;
    margin-bottom: 30px !important;
    font-size: 0.8125rem; }
  .tabular-list .list-subsection-header {
    color: #4F4F4F !important;
    font-weight: 600; }
  .tabular-list .no-entries {
    padding: 20px 0px 10px 0px;
    font-size: 1rem;
    font-weight: 400;
    color: #767676; }
  .tabular-list tr {
    height: inherit; }
  .tabular-list td {
    padding: 6px 10px 6px 10px;
    border: 0;
    color: #232323;
    font-size: 0.8125rem;
    line-height: 1.25rem;
    font-family: "Open Sans", Arial, sans-serif; }
  .tabular-list ul {
    list-style-type: none;
    padding: 0; }
    .tabular-list ul li {
      font-size: 0.8125rem;
      text-decoration: underline #0182D7;
      cursor: pointer; }
  .tabular-list tbody tr:nth-child(odd) {
    background-color: #F9F9F9; }
  .tabular-list tbody tr:nth-child(even) {
    background-color: #FFFFFF; }
  .tabular-list span.list-missing {
    border-bottom: 1px solid #EC9696;
    color: #767676; }
  .tabular-list span.list-unsigned span {
    border-bottom: 1.5px dashed #55C9CE;
    text-decoration: none; }
  .tabular-list span.list-captured span {
    border-bottom: 1px solid #55C9CE; }
  .tabular-list span.has-action-menu span {
    cursor: pointer; }
  .tabular-list .list-column-heading {
    color: #4F4F4F;
    background-color: #FFFFFF;
    padding: 1px 10px 2px 10px;
    border-bottom: 1px solid #DDDDDD;
    font-weight: 600 !important;
    font-family: "Open Sans", Arial, sans-serif;
    text-align: left; }
  .tabular-list span.stopped-cell {
    border: 0;
    color: #C80B0B;
    font-family: "Open Sans", Arial, sans-serif; }
  .tabular-list-tooltip .rc-tooltip-inner {
    width: 200px;
    min-height: 22px;
    text-align: center;
    border-radius: 6px; }
  .tabular-list .highlighted {
    background-color: #ffff03 !important; }
  .tabular-list .selected {
    background-color: #ff9632 !important; }
  .tabular-list .multi-column table {
    width: 98%; }
  .tabular-list .single-section td {
    width: auto !important; }

tr, td {
  font-size: 0.8125rem; }

h2.subsection {
  width: 98%;
  text-align: left;
  border-bottom: 1px solid #DDDDDD;
  line-height: 0.1em;
  margin: 20px 10px 10px 0px !important; }
  h2.subsection span {
    background: #FFFFFF;
    font-size: 0.8125rem;
    padding-right: 10px; }
  h2.subsection .highlighted {
    background-color: #ffff03 !important; }
  h2.subsection .selected {
    background-color: #ff9632 !important; }
