:export {
  state: #767676;
  bodyGray: #4F4F4F; }

#menu-items .menu-item {
  display: flex;
  padding: 15px 0;
  border-bottom: 1px solid #DDDDDD;
  border-top: 1px;
  transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
  align-items: center;
  box-shadow: #DDDDDD -1px 0px 1px 0px, #DDDDDD 1px 0px 1px 0px;
  font-size: 0.8rem;
  background: #FFFFFF; }
  #menu-items .menu-item:hover {
    background-color: #F0F0F0;
    cursor: pointer; }
  #menu-items .menu-item:first-child {
    border-top: 1px solid #F0F0F0;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px; }
  #menu-items .menu-item:last-child {
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px; }
  #menu-items .menu-item.selected {
    background-color: #028DEA;
    border: #028DEA;
    color: #FFFFFF; }
  #menu-items .menu-item .menu-item-name {
    margin: 0 0 0 20px;
    min-width: 95px;
    display: inline-block; }
    #menu-items .menu-item .menu-item-name:after {
      background: #DDDDDD;
      width: 1px;
      content: "";
      display: inline-block;
      top: 0;
      bottom: 0;
      right: 0;
      min-height: 20px;
      margin: -5px 20px -5px 20px; }
  #menu-items .menu-item .menu-item-description {
    margin: 0 20px 0 0;
    display: inline-block;
    max-width: 75%; }
