:export {
  state: #767676;
  bodyGray: #4F4F4F; }

.treatment-options-outcomes {
  margin: 20px 0 200px 0;
  font-size: 0.9em; }
  .treatment-options-outcomes__toggle {
    display: flex;
    justify-content: flex-end;
    font-size: 1.6em; }
    .treatment-options-outcomes__toggle .timescale-toggles {
      display: flex;
      height: 20px;
      margin-right: 40px; }
      .treatment-options-outcomes__toggle .timescale-toggles .timescale-toggle {
        display: flex;
        align-items: center;
        border-radius: 4px;
        font-size: 0.6em;
        padding: 2px 10px;
        color: #4F4F4F;
        cursor: pointer; }
        .treatment-options-outcomes__toggle .timescale-toggles .timescale-toggle.active {
          background-color: #028DEA;
          color: #FFFFFF; }
    .treatment-options-outcomes__toggle .toggle-icon {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-right: 15px;
      cursor: pointer;
      color: #767676; }
      .treatment-options-outcomes__toggle .toggle-icon svg {
        stroke: #767676; }
      .treatment-options-outcomes__toggle .toggle-icon.active,
      .treatment-options-outcomes__toggle .toggle-icon.active .toggle-text {
        color: #028DEA; }
        .treatment-options-outcomes__toggle .toggle-icon.active svg,
        .treatment-options-outcomes__toggle .toggle-icon.active .toggle-text svg {
          stroke: #028DEA; }
      .treatment-options-outcomes__toggle .toggle-icon .toggle-text {
        font-size: 0.6em;
        color: #4F4F4F; }
