:export {
  state: #767676;
  bodyGray: #4F4F4F; }

.treatment-options-outcomes-icons {
  display: flex;
  flex-direction: row;
  margin-top: 10px; }
  @media only screen and (max-width: 1024px) {
    .treatment-options-outcomes-icons {
      flex-direction: column; } }
  .treatment-options-outcomes-icons.note {
    font-style: italic; }
  .treatment-options-outcomes-icons .icons-survival-text {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin: 5px;
    width: 120px;
    height: 375px;
    font-weight: 100; }
  .treatment-options-outcomes-icons .icons-interaction *,
  .treatment-options-outcomes-icons .icons-interaction *:before,
  .treatment-options-outcomes-icons .icons-interaction *:after {
    box-sizing: inherit; }
  .treatment-options-outcomes-icons .icons-interaction {
    box-sizing: border-box;
    margin-right: 20px;
    border-collapse: separate;
    border-spacing: 0; }
    .treatment-options-outcomes-icons .icons-interaction td {
      padding: 0 10px;
      font-size: 1.1em; }
    .treatment-options-outcomes-icons .icons-interaction__header {
      margin-bottom: 10px; }
      .treatment-options-outcomes-icons .icons-interaction__header .user-icon,
      .treatment-options-outcomes-icons .icons-interaction__header .overall-survival {
        text-align: center; }
    .treatment-options-outcomes-icons .icons-interaction__row {
      height: 56px;
      cursor: pointer; }
      .treatment-options-outcomes-icons .icons-interaction__row:nth-child(odd) {
        background-color: #F9F9F9; }
      .treatment-options-outcomes-icons .icons-interaction__row:hover:not(.selected-treatment) {
        background-color: #eaf3fc; }
      .treatment-options-outcomes-icons .icons-interaction__row.selected-treatment {
        background-color: #ececec;
        box-shadow: 0px 10px 8px -5px #999999; }
      .treatment-options-outcomes-icons .icons-interaction__row.displayed-treatment td {
        border-color: #378EE3; }
        .treatment-options-outcomes-icons .icons-interaction__row.displayed-treatment td.treatment-name {
          border-top-left-radius: 5px;
          border-bottom-left-radius: 5px; }
        .treatment-options-outcomes-icons .icons-interaction__row.displayed-treatment td.overall-survival {
          border-top-right-radius: 5px;
          border-bottom-right-radius: 5px; }
      .treatment-options-outcomes-icons .icons-interaction__row .treatment-name,
      .treatment-options-outcomes-icons .icons-interaction__row .total-patients,
      .treatment-options-outcomes-icons .icons-interaction__row .overall-survival {
        border-top: 2px solid transparent;
        border-bottom: 2px solid transparent; }
      .treatment-options-outcomes-icons .icons-interaction__row .treatment-name {
        vertical-align: middle;
        border-left: 2px solid transparent; }
        .treatment-options-outcomes-icons .icons-interaction__row .treatment-name .select-icon {
          display: inline-block;
          padding-right: 10px;
          cursor: pointer; }
        .treatment-options-outcomes-icons .icons-interaction__row .treatment-name .display-name {
          display: inline-block; }
      .treatment-options-outcomes-icons .icons-interaction__row .total-patients,
      .treatment-options-outcomes-icons .icons-interaction__row .overall-survival {
        text-align: center; }
      .treatment-options-outcomes-icons .icons-interaction__row .overall-survival {
        border-right: 2px solid transparent; }
        .treatment-options-outcomes-icons .icons-interaction__row .overall-survival .diff-num-selected {
          font-size: 1.1em; }
          .treatment-options-outcomes-icons .icons-interaction__row .overall-survival .diff-num-selected.positive {
            color: #427E84; }
          .treatment-options-outcomes-icons .icons-interaction__row .overall-survival .diff-num-selected.negative {
            color: #EA7E73; }
    .treatment-options-outcomes-icons .icons-interaction .selected-icon {
      position: absolute;
      top: 6px;
      left: -15px; }
  .treatment-options-outcomes-icons .flex {
    display: flex; }
  .treatment-options-outcomes-icons .flex-1 {
    flex: 1; }
  .treatment-options-outcomes-icons .flex-2 {
    flex: 2; }
  .treatment-options-outcomes-icons .flex-3 {
    flex: 3; }
  .treatment-options-outcomes-icons .flex-padding {
    padding: 0 20px; }
  .treatment-options-outcomes-icons .flex-center {
    align-items: center; }
