:export {
  state: #767676;
  bodyGray: #4F4F4F; }

.name-tooltip .rc-tooltip-inner {
  width: auto;
  border-radius: 6px;
  text-align: center;
  min-height: 0px; }

#longitudinal-table .vertical-scroll {
  overflow-y: scroll !important;
  max-height: 200px; }
  #longitudinal-table .vertical-scroll .sticky-table .sticky-table-row:nth-child(-n + 2) .sticky-table-cell {
    position: -webkit-sticky;
    position: sticky;
    top: 0 !important;
    z-index: 2; }
  #longitudinal-table .vertical-scroll .sticky-table .sticky-table-row .sticky-table-cell:nth-child(-n + 4) {
    position: -webkit-sticky;
    position: sticky;
    left: 0;
    z-index: 2; }
  #longitudinal-table .vertical-scroll .sticky-table
.sticky-table-row:nth-child(-n + 2)
.sticky-table-cell:nth-child(-n + 4) {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    left: 0;
    z-index: 3; }
  #longitudinal-table .vertical-scroll .sticky-table {
