    width: auto;
    margin-top: 10px;
    font-size: 0.8125rem;
    overflow-x: unset !important; }
    #longitudinal-table .vertical-scroll .sticky-table.white-scrollbar::-webkit-scrollbar-thumb {
      background-color: #ffffff !important; }
    #longitudinal-table .vertical-scroll .sticky-table #sticky-header {
      top: 0 !important; }
    #longitudinal-table .vertical-scroll .sticky-table #sticky-name {
      left: 50px;
      min-width: 135px;
      max-width: 135px; }
    #longitudinal-table .vertical-scroll .sticky-table #sticky-unit {
      left: 185px;
      min-width: 95px; }
    #longitudinal-table .vertical-scroll .sticky-table #section-header {
