:export {
  state: #767676;
  bodyGray: #4F4F4F; }

.options-range-selector {
  position: absolute;
  top: 30px;
  left: 0;
  min-width: 300px;
  min-height: 100px;
  z-index: 1;
  padding: 5px 10px; }
  .options-range-selector .header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #F0F0F0;
    padding: 5px; }
    .options-range-selector .header .title .name {
      font-weight: 600; }
    .options-range-selector .header .title .unit {
      color: dimgray; }
    .options-range-selector .header .reset-range {
      font-size: 0.9em;
      color: #0079CA;
      cursor: pointer; }
      .options-range-selector .header .reset-range:hover {
        color: #005a97; }
  .options-range-selector .selector {
    margin: 40px 0; }

div.rc-slider-handle {
  height: 22px;
  margin-top: -10px;
  border-radius: 4px;
  width: 12px;
  margin-left: -6px;
  border: 1px solid #9c9c9c; }

div.rc-slider-rail {
  border: 1px solid #9c9c9c;
  background-color: white;
  height: 8px;
  margin-top: -2px; }

div.rc-slider-track {
  height: 6px;
  margin-top: -1px;
  background-color: #0079CA; }

div.rc-slider span.rc-slider-dot {
  height: 20px;
  width: 1px;
  border-radius: 0;
  background-color: #9c9c9c;
  bottom: -8px;
  border: 0;
  margin-left: -1px; }

div.rc-slider-tooltip-placement-top {
  z-index: 1; }

div.rc-slider-tooltip-arrow {
  border: 0; }

div.rc-slider-tooltip-inner {
  background-color: transparent;
  box-shadow: none;
  color: #0079CA;
  font-size: 1.1em;
  font-weight: 600;
  padding-top: 15px; }
